import { ChangeDetectorRef, Component, ViewChild, ViewEncapsulation } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { Router } from '@angular/router';
import { SettingsListConfig } from 'src/app/config/display/settings-list-config';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ChqWidgetsDropdownModel } from 'src/app/model/chq-widgets-dropdown-model';
import { ChqWidgetsInputModel, IControlValue } from 'src/app/model/chq-widgets-input-model';
import { CaseService } from 'src/app/services/case/case.service';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';
import { SettingsService } from 'src/app/services/settings/settings.service';
import { ChqDropdownComponent } from 'src/app/widgets/chq-dropdown/chq-dropdown.component';
import { SortChangedCallback, TableConfig, TableRowData } from 'src/app/widgets/chq-mat-table/chq-mat-table.component';
import { ApiPagination, PaginateInfo, defaultPagination } from 'src/app/widgets/chq-paginator/chq-paginator.component';

@Component({
  selector: 'app-list-view',
  templateUrl: './list-view.component.html',
  styleUrls: [ './list-view.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class ListViewComponent {

  /**
   * constructor
   * @param router 
   */
  public tableConfig: TableConfig;
  public tableData: TableRowData[];
  public pageChanged: boolean = false;
  public sortChanged: any;
  public pagination: ApiPagination = defaultPagination;
  public static currentPage: number = 1;
  public totalCount: number = 100;
  private readonly configData: any = SettingsListConfig
  public static filterDetail: any = [];
  public queryParams: string = '';
  public organizationList: Array<any>;
  public userEmailDetail: string = '';
  public userOrg: any;

  @ViewChild('org') org: ChqDropdownComponent;

  public input: any = { org: false, name: false };

  public applyButtonModel: ChqWidgetsButtonModel = {
    label: 'apply',
    type: 'primary',
    icon: ''
  }

  public resetButtonModel: ChqWidgetsButtonModel = {
    label: 'reset_all',
    type: 'outline',
    icon: ''
  }

  public dropdownModelFilter: ChqWidgetsDropdownModel = {
    options: [ { label: 'Select...' }, { label: 'Name' }, { label: 'Organization' } ],
    placeHolder: 'select_dot',
    label: 'select_type',
    name: 'filterDropdown',
    displayValue: 'label',
    selectedOption: { 'label': 'Select...' }
  };

  public dropdownModelOrganization: ChqWidgetsDropdownModel = {
    options: [],
    placeHolder: 'organization',
    label: 'select_organization',
    name: 'orgDropdown',
    displayValue: 'name',
    selectedOption: []
  }

  public nameTextBox: ChqWidgetsInputModel = {
    placeHolder: 'name',
    label: 'enter_name',
    name: 'name',
    type: 'text',
    value: '',
    required: false
  };

  i18n: i18nInfo;

  /**
   * constructor
   * @param router 
   */
  constructor(
    private settingsService: SettingsService,
    private commonService: CommonService,
    private cd: ChangeDetectorRef,
    private monitorService: MonitorService,
    private caseService: CaseService,
    private router: Router
  ) { 
    this.i18n = this.commonService.geti18nInfo();
  }

  /**
     * ngOnInit
     */
  ngOnInit(): void {
    this.monitorService.logEvent('ngOnInit', [ 'ListViewComponent', 'addenda-repair' ]);
    this.tableConfig = this.configData;
    this.applyButtonModel.onclick = this.filterCases.bind(this);
    this.resetButtonModel.onclick = this.resetFilter.bind(this);
    this.commonService.showLoading();
    this.applyButtonModel.type = 'disabled';
    this.userEmailDetail = this.commonService.userProfileData.value?.data?.email;
    this.userOrg = this.commonService.userProfileData.value?.data.organizationDetail;
    this.caseService.getBMOrganizations().subscribe({
      next: (response) => {
        this.organizationList = response.data;
        this.dropdownModelOrganization = { ...this.dropdownModelOrganization, ...{ options: this.organizationList } };
        this.getEmployees();
      },
      error: (err: any) => {
        this.monitorService.logEvent('getOrganizations', [ 'ListViewComponent', 'addenda-repair', {
          err
        } ]);
        this.getEmployees();
        this.commonService.hideLoading();
      }
    });
  }

  handleSortChange: SortChangedCallback = (sort: Sort): void => {
    if (sort) {
      this.pagination = { ...this.pagination, ...{ orderBy: sort.active, sortDirection: sort.direction } };
      this.commonService.showLoading();
      this.getEmployees();
    }
  }

  /**
  * handlePaginationChange
  * @param event
  */
  handlePaginationChange(event: PaginateInfo): void {
    const pagination: ApiPagination = {
      pageSize: event.pageSize,
      offset: event.offset,
      currentPage: event.currentPage
    };

    this.pagination.currentPage = event.currentPage;
    this.pagination = { ...this.pagination, ...pagination };
    this.pageChanged = true;
    this.getEmployees();
  }

  /**
   * filter hide
   */
  hideDropdown(name: string): void {
    if (name === 'filterDropdown') {
      if (this.org) {
        this.org.closeDropdown();
      }
    }
  }

  /**
  * filter cases
  */
  public filterCases(): void {
    const selectedFilter = this.dropdownModelFilter.selectedOption?.label;
    this.queryParams = '';
    switch (selectedFilter?.toLowerCase()) {
    case 'name':
      if (this.nameTextBox.value != '') {
        const empName = this.nameTextBox.value;
        this.queryParams = `(contains(tolower(FirstName),'${empName}')) or (contains(tolower(LastName),'${empName}'))`;
      }
      break;
    case 'organization':
      this.queryParams += `OrgName eq '${this.dropdownModelOrganization?.selectedOption?.name}' OR `;
      break;
    default:
      break;
    }

    if (this.queryParams != '') {
      if (this.queryParams.indexOf('OR') !== -1) {
        this.queryParams = this.queryParams.substring(0, this.queryParams.lastIndexOf('OR'));
      }
    }
    this.monitorService.logEvent('filter employee', [ 'ListViewComponent', 'addenda-repair', {
      selectedFilter
    } ]);
    this.getEmployees();
  }

  /**
   * selected any filter in the dropdown
   * @param $event 
   */
  public selectFilter($event: any): void {
    this.monitorService.logEvent('selectFilter', [ 'ListViewComponent', 'addenda-repair', {
      $event
    } ]);
    if ($event?.name == 'filterDropdown') {
      this.nameTextBox.value = '';
      this.dropdownModelFilter.selectedOption = $event.value
      this.dropdownModelOrganization.selectedOption = [];
      switch ($event.value[this.dropdownModelFilter.displayValue]?.toLowerCase()) {
      case 'organization':
        this.hideShowDropdown('org');
        this.dropdownModelOrganization = { ...this.dropdownModelOrganization, selectedOption: this.userOrg };
        this.dropdownModelOrganization.selectedOption ? this.applyButtonModel.type = 'primary' : this.applyButtonModel.type = 'disabled';
        this.filterCases();
        break;
      case 'name':
        this.applyButtonModel.type = 'disabled';
        this.hideShowDropdown('name');
        break;
      default:
        this.applyButtonModel.type = 'disabled';
        this.hideShowDropdown('');
        break;
      }
    } else if ($event?.name == 'orgDropdown') {
      $event.value != undefined ? this.applyButtonModel.type = 'primary' : this.applyButtonModel.type = 'disabled'
      this.dropdownModelOrganization.selectedOption = $event.value;
    }

    if ($event?.name == 'filterDropdown') {
      ListViewComponent.filterDetail['mainFilter'] = $event;
    } else {
      ListViewComponent.filterDetail['subFilter'] = $event;
    }
  }

  /**
   * 
   * @param event 
   */
  public updateInput(event: IControlValue): void {
    this.nameTextBox.value = event.value;
    if (event.value != '') { this.applyButtonModel.type = 'primary' } else { this.applyButtonModel.type = 'disabled' }
  }

  /**
   * hide show dropdown
  */
  public hideShowDropdown(key: string): void {
    this.input[key] = true;
    Object.keys(this.input).forEach(
      (value) => {
        if (value != key) {
          this.input[value] = false;
        }
      }
    )
  }

  /**
   * reset Filter
   */
  resetFilter(): void {
    this.applyButtonModel.type = 'disabled';
    this.nameTextBox = { ...this.nameTextBox, value: '' }
    const obj = {
      name: 'filterDropdown',
      type: 'select',
      value: { label: 'Select...' }
    }
    this.selectFilter(obj);
    this.filterCases();
  }

  /**
  * getEmployeeList
  */
  public getEmployees(): void {
    this.commonService.showLoading();
    this.settingsService.getEmployees(this.pagination, this.queryParams).subscribe({
      next: (response) => {
        this.tableData = response ? response?.value : [];
        if (this.userEmailDetail && response?.value.map((item: any) => (item.Email)).includes(this.userEmailDetail)) {
          let tempArr: any;
          // eslint-disable-next-line prefer-const
          tempArr = this.tableData;
          const record = this.tableData.find((ele: any) => ele.Email === this.userEmailDetail);
          this.tableData = tempArr.filter(item => item.Email !== this.userEmailDetail);
          this.tableData.unshift(record);
        }
        this.pagination.count = response ? response['@odata.count'] : 0;
        this.pagination = { ...this.pagination };
        this.cd.detectChanges();
        this.commonService.hideLoading();
      },
      error: () => {
        this.commonService.hideLoading();
      }
    })
  }

  /**
   * cellClickedCallback
   */
  cellClickedCallback = (row, col, $event): void => {
    console.log(row, col);
    const employeeId = row.filter(item => item.id == 'EmployeeGuid')[0]?.result.value ?? 0;
    if (employeeId != 0)
      this.router.navigate([ `/settings/employees/edit/${employeeId}` ]);
  }
}





